import React, {useState, useEffect} from 'react';
import axios from 'axios';

export default function Tags() {
    const [tags, setTags] = useState([]);
    let tagNames = '';
    useEffect(() => {
        axios
            .get(`https://api.stackexchange.com/2.2/tags?order=desc&sort=popular&site=stackoverflow&key=eZoZLL*)llKhER8pMMusAw((`)
            .then((resp) => {
                setTags(resp.data.items);
                resp.data.items.map(tag => {
                    tagNames += `${tag.name};`
                })
                tagNames = tagNames.substring(0, tagNames.length-1);
                console.log(tagNames);
            })
    })
    return (
        <div>
            <h1>
                tags page 2
            </h1>
        </div>
    )
}