import React, {useEffect, useState} from 'react';
import axios from 'axios';

export default function AssociateUser(props) {
    const [user, setUser] = useState([]);
    let userIds = '';
    props.associate.map(userr => {
        userIds += `${userr.user_id};`;
    })
    let usersIds = userIds.substring(0, userIds.length-1);
    console.log(usersIds);
        useEffect(()=>{
            if(usersIds.length > 0){
            axios.get(`https://api.stackexchange.com/2.2/users/${usersIds}?order=desc&sort=reputation&site=stackoverflow&key=eZoZLL*)llKhER8pMMusAw((`)
            .then((resp)=>{
                setUser(resp.data.items);
            })
        }}, [window.location.href])  
    console.log(user);
        return (
            <>
                {user.map((auser, index) => {
                    return(
                        <div key={index} className='associated-user' onClick={() => window.location.href=`#/profile/${auser.user_id}`}>
                            <img src={auser.profile_image} />
                            <p dangerouslySetInnerHTML={{__html:auser.display_name}}></p>
                        </div>
                    )
                })}
                </>
        )
}
