import React, { useEffect} from 'react';
import axios from 'axios';
import { __RouterContext } from 'react-router';
import QuestionItem from '../Components/QuestionItem';

function Home (){
        const [questions, setDataQ] = React.useState([]);
        let link = '';
        useEffect(()=>{
          const apiUrl = 'https://api.stackexchange.com/2.2/questions?order=desc&sort=activity&site=stackoverflow&key=eZoZLL*)llKhER8pMMusAw((' ; //
          axios.get(apiUrl).then((resp) => {
            const allQuestions = resp.data;
            console.log(allQuestions);
            setDataQ([...questions, ...allQuestions.items]);
            console.log(questions);
          });
        }, [window.location.href])
        // console.log(questions);
        
        function routeTo(linkTo){
          link = linkTo;
          //<Question linkTo={linkTo}/>
          window.location.href = `#/question/:${link}`;
        }

        

        return (
            <div>
                <h1>Main page</h1>
                {questions.map((question, index)=>{
                  return(
                    <QuestionItem question={question} key={index} />
                  )
                })}
            </div>
        )
    
}

export default Home;